/***** time-line*****/
.timeline__item:after {
  background: #fff !important;
}

.timeline__content {
  background-color: #fff;
  border-radius: 10px;
  display: block;
  padding: 1.25rem;
  position: relative;
}

/*---- Time line -----*/
.timeline {
  position: relative;
  margin: 0 0 2rem;
  padding: 0;
  list-style: none;
  inset-block-start: 2px;
}
.timeline:before {
  background-color: #e9ecef;
  position: absolute;
  display: block;
  content: "";
  height: 100%;
  inset-block-start: 0;
  inset-block-end: 0;
  inset-inline-start: 4px;
}

.timeline-item {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  padding-inline-start: 2rem;
  margin: 0.5rem 0;
}
.timeline-item:first-child:before, .timeline-item:last-child:before {
  content: "";
  position: absolute;
  background: #fff;
  width: 1px;
  inset-inline-start: 0.25rem;
}
.timeline-item:first-child {
  margin-block-start: 0;
}
.timeline-item:first-child:before {
  inset-block-start: 0;
  height: 0.5rem;
}
.timeline-item:last-child {
  margin-block-end: 0;
}
.timeline-item:last-child:before {
  inset-block-start: 0.5rem;
  inset-block-end: 0;
}

.timeline-badge {
  position: absolute;
  display: block;
  width: 0.4375rem;
  height: 0.4375rem;
  inset-inline-start: 1px;
  inset-block-start: 0.5rem;
  border-radius: 100%;
  border: 1px solid #fff;
  background: #adb5bd;
}

.timeline-time {
  white-space: nowrap;
  margin-inline-start: auto;
  color: #9aa0ac;
  font-size: 87.5%;
}

.timeline__item--right .timeline__content:before {
  border-inline-end: 12px solid rgba(238, 232, 239, 0.9);
}

.timeline__items img {
  border-radius: 7px;
}

ul.timeline {
  list-style-type: none;
  position: relative;
}
ul.timeline:before {
  content: " ";
  background: #d4d9df;
  display: inline-block;
  position: absolute;
  inset-inline-start: 29px;
  width: 2px;
  height: 100%;
  z-index: 400;
}
ul.timeline > li {
  margin: -5px 42px;
  padding-inline-start: 20px;
}
ul.timeline > li:before {
  content: " ";
  display: inline-block;
  position: absolute;
  border-radius: 50%;
  border: 1px solid #6c6c6f;
  inset-inline-start: 20px;
  width: 20px;
  height: 20px;
  z-index: 400;
}

/*----Timeline---*/
.vtimeline {
  list-style: none;
  padding: 0;
  position: relative;
  margin-block-end: 20px;
}
.vtimeline:before {
  inset-block-start: 0;
  inset-block-end: 0;
  position: absolute;
  content: " ";
  width: 3px;
  background-color: #d1d4e4;
  inset-inline-start: 50%;
  margin-inline-start: -1.5px;
}
.vtimeline .timeline-wrapper {
  display: block;
  margin-block-end: 20px;
  position: relative;
  width: 100%;
  padding-inline-end: 90px;
}
.vtimeline .timeline-wrapper:before {
  content: " ";
  display: table;
}
.vtimeline .timeline-wrapper:after {
  content: " ";
  display: table;
  clear: both;
}
.vtimeline .timeline-wrapper .timeline-panel {
  border-radius: 2px;
  padding: 20px;
  position: relative;
  background: #fff;
  border-radius: 5px;
  box-shadow: 0 8px 16px 0 rgba(162, 169, 204, 0.24);
  width: 35%;
  margin-inline-start: 14%;
}
.vtimeline .timeline-wrapper .timeline-panel:before {
  position: absolute;
  inset-block-start: 0;
  width: 100%;
  height: 2px;
  content: "";
  inset-inline-start: 0;
  inset-inline-end: 0;
}
.vtimeline .timeline-wrapper .timeline-panel:after {
  position: absolute;
  inset-block-start: 10px;
  inset-inline-end: -10px;
  display: inline-block;
  border-block-start: 10px solid transparent;
  border-inline-start: 10px solid #e9edf4;
  border-inline-end: 0 solid #e9edf4;
  border-block-end: 10px solid transparent;
  content: " ";
}
.vtimeline .timeline-wrapper .timeline-panel .timeline-title {
  font-size: 1rem;
  font-weight: 700;
  margin: 0 0 0.625rem;
}
.vtimeline .timeline-wrapper .timeline-panel .timeline-body p + p {
  margin-block-start: 5px;
}
.vtimeline .timeline-wrapper .timeline-panel .timeline-body ul {
  margin-block-end: 0;
}
.vtimeline .timeline-wrapper .timeline-panel .timeline-footer span {
  font-size: 0.6875rem;
}
.vtimeline .timeline-wrapper .timeline-badge {
  width: 55px;
  height: 55px;
  position: absolute;
  inset-block-start: -2px;
  inset-inline-start: calc(50% - 26px);
  z-index: 0;
  border-radius: 50px;
  border: 2px solid #fff;
}
.vtimeline .timeline-wrapper .timeline-badge i {
  color: #fff;
}
.vtimeline .timeline-wrapper.timeline-inverted {
  padding-inline-end: 0;
  padding-inline-start: 90px;
}
.vtimeline .timeline-wrapper.timeline-inverted .timeline-panel {
  margin-inline-start: auto;
  margin-inline-end: 14%;
}
.vtimeline .timeline-wrapper.timeline-inverted .timeline-panel:after {
